body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f9f5f5
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.floating-right {
  position: fixed;
  bottom: 25px;
  right: 25px;
  z-index: 2;
}

.main-elevation {
  background: #fff;
}


/* Show whole content of seconday text*/
.mdc-list.extendSecondaryText .mdc-list-item {
  height: auto !important;
  height: initial !important;
}

.extendSecondaryText .mdc-list-item__secondary-text {
  white-space: normal;
  white-space: initial;
}

.print {
  display: none;
}

.receipt {
  text-align: center;
}

.receipt .header p {
  font-weight: 500;
  margin: 0.1rem;
}

table.receipt {
  min-width: 15rem;
  padding: 1rem;
}

table.receipt>tbody>tr>:nth-child(1) {
  padding-right: 10px;
  text-align: left;
}
table.receipt>tbody>tr>:nth-child(2) {
  padding-left: 10px;
  text-align: right;
}

table td.bill-split{
  border-bottom: 1px dashed black;
}

table td.bill-payment {
  padding-top: 15px;
}

@media print {
  body {
    background-color: #fff !important;  
  }
  .no-print {
    display: none;
  }

  .main-elevation {
    box-shadow: none;
  }

  .print {
    display: block;
  }
}
.mdc-drawer__content .mdc-list a {
  color: inherit;
  text-decoration: none;
}

.mdc-top-app-bar--fixed input {
  width: -webkit-fill-available;
  margin-left: 10px;
  line-height: 2rem;
  font-size: 1.25rem;
  border: none;
  outline: none;
}

.mdc-top-app-bar--fixed .search-field {
  background: #fff;
}

.search-field.mdc-top-app-bar__section .rmwc-icon.material-icons.mdc-top-app-bar__action-item {
  color: #000;
}

.mdc-card {
  margin: 1rem;
}

.mdc-card a {
  color: inherit;
  text-decoration: none;
}

#masonry-layout {
  margin: auto;
}

.highlight {
  width: 40px;
  height: 40px;
  display: -webkit-inline-flex;
  display: inline-flex;
  position: relative;
  overflow: hidden;
  font-size: 1.25rem;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  -webkit-align-items: center;
          align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 50%;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #ff6659;
}

.done i{
  color: #018786;
}
.mdc-text-field {
  width: -webkit-fill-available;
  margin: .5rem;
}

.mdc-select {
  width: -webkit-fill-available;
  margin: .5rem;
}

.main-elevation button {
  margin: 1rem;
}

.main-elevation {
  margin: .5rem;
}

.mdc-text-field--focused .mdc-floating-label {
  color: black !important;
}
.mdc-select .mdc-floating-label {
  color: black !important;
}

.order-details .mdc-chip {
  padding: 25px 15px;
  margin: 10px;
  font-size: 1rem;
}

.mdc-list .mdc-list-item {
  height: 55px;
}
